import {authAPI, securityApi, usersAPI} from "../api/api";
import {stopSubmit} from "redux-form";
import avatarUser from "./../assets/images/avatar.svg";

const SET_USER_DATA = "SET_USER_DATA";
const SET_CAPTCHA = "SET_CAPTCHA";
const UPDATE_PHOTO = "UPDATE_PHOTO";

export type InitialStateAuthType = {
    userId: number | null,
    email: string | null,
    login: string | null,
    photos: string | null,
    isAuth: boolean,
    captcha: string | null
}

const initialState: InitialStateAuthType = {
    userId: null,
    email: null,
    login: null,
    photos: null,
    isAuth: false,
    captcha: null
}

function authReducer(state: InitialStateAuthType = initialState, action: any): InitialStateAuthType {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.data,
            }
        case SET_CAPTCHA:
            return {
                ...state,
                captcha: action.captcha
            }
        case UPDATE_PHOTO:
            return {
                ...state,
                photos: action.photos
            }
        default:
            return state;
    }
}

type UserType = {
    userId: number | null,
    email: string | null,
    login: string | null,
    photos: string | null,
    isAuth: boolean
}

export type SetUserDataActionType = {
    type: typeof SET_USER_DATA,
    data: UserType
}
export const setAuthUserData = (userId, email, login, photos, isAuth): SetUserDataActionType => {
    return {
        type: SET_USER_DATA,
        data: {
            userId,
            email,
            login,
            photos,
            isAuth
        }
    }
}

const setCaptcha = (captcha) => {
    return {
        type: SET_CAPTCHA,
        captcha
    }
}

export const updateAuthPhoto = (photo) => {
    return {
        type: UPDATE_PHOTO,
        photos: photo
    }
}

export const authThunkCreate = () => async (dispatch) => {
    let response = await authAPI.authMe();
    if (response.resultCode === 0) {
        let {id, email, login} = response.data
        usersAPI.getProfileUser(id).then(response => {
            let photos = response.photos.small;
            if (!photos) {
                photos = avatarUser;
            }
            dispatch(setAuthUserData(id, email, login, photos, true));
        })
    }
}

export const loginThunkCreate = (email, password, rememberMe, captcha) => async (dispatch) => {
    let response = await authAPI.authLogin(email, password, rememberMe, captcha);

    if (response.data.resultCode === 0) {
        dispatch(authThunkCreate());
        dispatch(setCaptcha(null))
    } else {
        let message = (response.data.messages && response.data.messages.length > 0) ? response.data.messages[0] : '';
        dispatch(stopSubmit("login", {_error: message}));
        if (response.data.resultCode === 10) {
            dispatch(getCaptchaUrl());
        }
    }
}

export const logoutThunkCreate = () => async (dispatch) => {
    let response = await authAPI.authLogout();
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, null, false))
    }
}

export const getCaptchaUrl = () => async (dispatch) => {
    let response = await securityApi.getCaptcha();
    dispatch(setCaptcha(response.data.url))
}
export default authReducer;

